.btn {
    border-radius: 3px;
    font-size: 13px;
    font-weight: 700;
    height: 50px;
    min-width: 170px;
    line-height: 28px;
    padding: 5px;
    display: inline-flex;
    align-items: center;
    justify-content: center;


    &.btn-medium { 
        min-width: 160px;
        height: 58px;
        line-height: 36px;
    }
    &.btn-lg {  /* Landing-1,2,4,5 */
        min-width: 172px;
        height: 50px;
        border-radius: 5px;
        &-1 {  /* Landing-1,2 */
            min-width: 137px;
            height: 40px;
            border-radius: 3px;
        }
        &-2 {  /* Landing-4 */
            min-width: 195px;
            height: 50px;
            border-radius: 3px;
        }
        &-3 {  /* Landing-5 */
            min-width: 190px;
            height: 50px;
            border-radius: 3px;
        }
        &-4 {  /* Landing-5 */
            min-width: 184px;
            height: 45px !important;
        }
        
    }
    
    
    &.btn-xl {  /* Landing-1,2,4,5,6 */
        min-width: 205px;
        height: 60px;
        border-radius: 3px;
    }
    &.btn-xl-custom {  /* Landing-5 */
        width: 205px;
        height: 60px;
        border-radius: 3px;
    }
    &.btn-xxl {  /* Landing-2,6 */
        min-width: 225px;
        max-width: 225px;
        height: 60px;
        border-radius: 3px;
    }
    &.btn-xxl-1 {  /* Landing-2,6 */
        min-width: 225px;
        height: 50px;
        border-radius: 3px;
        max-width: 225px;
    }
    &.btn-xxl-2 {  /* Landing-12 */
        min-width: 224px;
        height: 55px;
        border-radius: 0px;
        max-width: 224px;
    }
    &.btn-xxxl {  /* Landing-7 */
        min-width: 238px;
        max-width: 238px;
        height: 60px;
    }
    &.btn-278 {  /* Landing-7 */
        min-width: 278px;
        max-width: 278px;
        height: 57px;
    }
    &.btn-285 {  /* Landing-7 */
        min-width: 285px;
        max-width: 85px;
        height: 57px;
    }

    &.with-icon {

        i {
            margin-left: -5px;
            margin-right: 3px;
            font-size: 16px;
            margin-top: 2px;
        }
    }
    &-reset{
        background:transparent;
        border: 0;
    }
}

.btn-emerald{
    color: #fff !important;
    background: #4ebe86 !important;
}