
@mixin dark-theme-mode{
  --bg:                 #19191b;
  --bg-1:               #1e1e20;
  --bg-2:               #1e1e20;
  --bg-3:               #1e1e20;
  --bg-4:               #030303;
  --bg-5:               #1e1e20;
  --bg-6:               #1e1e20;
  --bg-7:               #1e1e20;
  --bg-8:               #1e1e20;
  --bg-9:               #FAF9F6;
  --color-headings:     #fff;
  --color-texts:        rgba(255,255,255,0.6);
  --color-texts:        #fff;
  --color-texts-2:      #adb4b7;
  --color-texts-3:      #adb4b7;
  --color-texts-4:      #adb4b7;
  --color-texts-5:      #adb4b7;
  --border-color:       rgba(255,255,255,.7);
  --border-color-2:     rgba(255,255,255,.2);
  --border-color-3:     #e5e5e5;
  --border-color-4:     rgba(80, 227, 194, 0.14);
  --border-color-5:     #2780ff;
}
@mixin light-theme-mode{
  --bg:                 #fff;
  --bg-1:               #F8F8F8;
  --bg-2:               #F3F8FE;
  --bg-3:               #FAFAFA;
  --bg-4:               #e3eef8;
  --bg-5:               #eef4f5;
  --bg-6:               #f7fbfc;
  --bg-7:               #F0F0F0;
  --bg-8:               #f4fcff;
  --bg-9:               #FAF9F6;
  --color-headings:     #1f1f1f;
  --color-texts:        #222f3f;
  --color-texts-2:      #000;
  --color-texts-3:      #333;
  --color-texts-4:      #323232;
  --color-texts-5:      #222d3d;
  --border-color:       #e5e5e5;
  --border-color-2:     #e5e5e5;
  --border-color-3:     #e5e5e5;
  --border-color-4:     rgba(80, 227, 194, 0.14);
  --border-color-5:     #2780ff;
}


// #FCFDFE, #F4F7FA, #F8F8F8, #ECF2F7, #FDFDFF, 


@mixin dark-mode {
  [data-theme='dark'] & ,.dark-mode-texts & {
      @content;
  }
}
@mixin light-mode {
  [data-theme='dark'] & ,.dark-mode-texts & {
      @content;
  }
}
