
/* Used in Landing-6 */
.accordion-trigger{
  &.arrow-icon{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:after{
      content: "\ea05";
      font-family: "Grayic";
      font-size: 30px;
      display: block;
      line-height: 1;
      transform: rotate(0deg);
      transition: .4s;
      color: #878b90;
      top: 7px;
      position: absolute;
      right: -10px;
      @include brk-point-range(480px, 991px){
        top: 0;
      }
    }
    &[aria-expanded="true"]{
      &:after{
        transform: rotate(-180deg);
      }
    }
  }
  &-2 {
    &.arrow-icon {
      &:after{
        top: 27px;
        right: 23px;
      }
    }
  }
}
  

/* Used in Landing-6 */
.accordion2-single-accordion {
  .accordion-trigger[aria-expanded="true"] {
    background: #fff !important;
    color: #000 !important;
  }
  .accordion-trigger[aria-expanded="true"]:hover, .accordion-trigger[aria-expanded="true"]:focus {
    background: $white !important;
  }
  .accordion-trigger[aria-expanded="false"]:hover, .accordion-trigger[aria-expanded="false"]:focus {
    background: $mineralgreen !important;
  }
  .show {
    background: #fff !important;
    color: #666 !important;
  }

}


