
/* Used in Landing-6 */
.hover {
    /* Used in Landing-6,cards.njk */
    &-shadow{
        /* Used in Landing-6,cards.njk */
        &-1 {
            transition: .4s;
            &:hover {
                box-shadow: $shadow-6;
                transition: .4s;
            }
        }
        /* Used in Landing-6 */
        &-2 {
            transition: .4s;
            &:hover {
                box-shadow: $shadow-8;
                transition: .4s;
            }
        }
        /* Used in Landing-6 */
        &-3 {
            transition: .4s;
            &:hover {
                box-shadow: $shadow-11;
                transition: .4s;
            }
        }
        &-4 {
            transition: .4s;
            &:hover {
                box-shadow: $shadow-10;
                transition: .4s;
            }
        }
    }

    /* Used in Landing-6,cards.njk */
    &-bg {
        &-1 {
            transition: .4s;
            &:hover {
                background: $emerald !important;
                color: $white;
            }
        }
    }
}