
/* Used in Landing-1,2,3,4,5,6 Header area */
.site-header--sticky.scrolling .navbar.site-navbar {
    padding-top: 10px !important;
}

/* Used in Landing-2,4,5 */
.img-grayscale {
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
}

/* Used in Landing-2 */
.font-wieght-extra-bold {
    font-weight: 800;
}

/* Used in custom.js */
.l2-slide-btn {
    display: inline-block;
    border: none;
    background: transparent;
    position: absolute;
    bottom: 44px;
    &.slick-prev {
        left: -87px;
        @include brk-point-range(480px, 767px){
            left: -50px;
        }
    }
    
    &.slick-next {
        left: auto;
        right: -87px;
        // transform: rotate(180deg);
        @include brk-point-range(480px, 767px){
            right: -50px;
        }
    }
}

/* Used in Landing-3 */
.footer-site-header {
    .main-menu > li > .nav-link {
        display: inline-block;
        &.gr-toggle-arrow {
            color: var(--color-texts-3) !important;
            padding: 15px 0 !important;
            &:before {
                width: 100%;
            }
        }
        &:before {
            top: auto;
            bottom: 0;
            background: $dodgerblue;
        }
    }
}
  /* landing-3 content section,  */
.img {
    &-pos-1 {
        position: relative;
        @include desktops{
            position: absolute;
            bottom: 0;
        }
    }
    &-pos-2 {
        position: relative;
        @include desktops{
            position: absolute;
            bottom: -120px;
        }
    }
    &-pos-3 {
        position: relative;
        @include desktops{
            position: absolute;
            bottom: 0;
        }
    }
    
} 

/* Used in Landing-4 */
.content-bg-shape {
    opacity: .2;
}

/* by default css */
.border-box {
    box-sizing: border-box;
}

/* used in Landing-5 content-3 section */
.card-columns {
    @include desktops{
        -moz-column-count: 2;
        -webkit-column-count: 2;
        column-count: 2;
    }
    @include brk-point-range(480px, 767px){
        -moz-column-count: 1;
        -webkit-column-count: 1;
        column-count: 1;
    }
    @include brk-point-range(768px, 991px){
        -moz-column-count: 2;
        -webkit-column-count: 2;
        column-count: 2;
    }
}

/* Used in Landing-6 */
.translateY-n50{
    transform:translateY(-50%);
}
.translateY-35{
    transform:translateY(35%);
}

 /* used in Landing-6 tab content list */
.list-border-before:before {
    position: absolute;
    content: "";
    left: 0;
    top: 10px;
    width: 1px;
    height: 18px;
    background: $bermuda;
}

/* Used in Landing-6 */
.bg-color {
    &-l6 {
        height: 92%;
    }
}


/* landing-6 blog-slider slider buttons */
.l6-slide-btn {
    position: absolute;
    right: 15px;
    top: -98px;
    border-radius: 50%;
    border: 1px solid #ddf7ea;
    background: #ddf7ea;
    transition: .4s;

    @include brk-point-max(767px){
        top: -40px;
    }

    @include brk-point-range(768px, 991px){
        top: -40px;
    }

    &:hover {
        background: $emerald;
        color: $white !important;
    }
    &.slick-prev {
        right: 77px;
    }
}

/* landing-7 screenshot-slider slider buttons */
.l7-slide-btn {
    position: absolute;
    right: 31%;
    top: 49%;
    border-radius: 50%;
    border: 1px solid #fff;
    background: #fff;
    transition: .4s;
    z-index: 1;

    @include brk-point-max(767px){
        left: 0;
    }

    @include brk-point-range(576px, 767px){
        left: 12%;
    }

    @include brk-point-range(768px, 991px){
        left: 12%;
    }

    @include brk-point-range(992px, 1200px){
        left: 23%;
    }

    &:hover {
        background: $dodgerblue2;
        color: $white !important;
        border: 1px solid $dodgerblue2;
    }
    &.slick-next {
        left: 31%;
        @include brk-point-max(767px){
            left: auto;
            right: 0;
        }
        @include brk-point-range(576px, 767px){
            right: 12%;
        }
        @include brk-point-range(768px, 991px){
            left: auto;
            right: 12%;
        }
        @include brk-point-range(992px, 1200px){
            left: auto;
            right: 23%;
        }
    }
}

/* Used in Landing-1,2,3,5 */
a.hover-color-primary {
    transition: .4s;
    &:hover {
        color: $bluechil !important;
    }
}

.rounded-50p {
    border-radius: 50%;
}

.footer-menu {
    .main-menu > {
        li > {
            a.nav-link {
                line-height: 28px;
                letter-spacing: -0.64px;
                font-size: 15px;
                font-weight: 700;
                position: relative;
                padding: 0 3px 10px !important;
                &:before {
                    position: absolute;
                    content: "";
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 1px;
                    background: #258aff;
                    opacity: 0;
                    visibility: hidden;
                }
                
                &:hover {
                    color: #258aff !important;
                    &::before {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
    }
}

