/* ========== Homepage 6 ========== */

.screenshot-slider-wrapper {
    position: relative;
    margin-top: 20px;

}

.screenshots-dots {
    .slick-dots {
        padding: 0;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        list-style: none;
        display: flex;
        align-items: center;
        margin: 0 4.5px;

        li {
            
            border: none;
            list-style: none;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: $white;
            color: transparent;
            opacity: 0.3;

                &:focus {
                    outline: none;
                    box-shadow: none;
                }

            &.slick-active {
                background-color: $white !important;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                opacity: 1;
            }
        }
    }
}

.screenshot-image {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    overflow: hidden;
    min-height: 720px !important;
    max-height: 720px !important;

    @include brk-point(690px) {
        width: 274px;
        min-height: 720px !important;
        max-height: 720px !important;
        margin-bottom: 0;
    }

    img {
        @include brk-point(690px) {
            width: 100%;
            border-radius: 20px;
        }
    }
}

.phone-bg-img {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 11;
    display: none;

    @include brk-point(690px) {
        display: block;
        transform: translate(-50%, -50%);
    }
}

.screenshot-slider {
    .slick-list {
        margin: 0 -23.5px;
    }

    .slick-slide {
        opacity: .5;
        transition: 0.4s;
    }

    .slick-center {
        opacity: 1;
        border-radius: 40px;
        max-height: 720px !important;
        transform: scale(1.1);
        min-height: 690px;

        img {
            border-radius: 40px;
        }

    }
}


.after-border:after {
    position: absolute;
    content: "";
    right: -15px;
    top: 46%;
    height: 30%;
