
/* Used in Landing-2 */
.rotate{
    &-n3 {
        transform: rotate(-3deg);
    }
    &-n4 {
        transform: rotate(-4deg);
    }
    &-n6 {
        transform: rotate(-6deg);
    }
    &-6 {
        transform: rotate(6deg);
    }
}


[data-aos="newsletter-animaion"] {
    transform: rotate(0)!important;
    &.aos-animate {
        transform: rotate(-4deg)!important;
    }
  }