.main-menu {
  /* ----------------------
Custom toggle arrow 
------------------------*/
}

@media (min-width: 992px) {
  .main-menu {
    display: flex;
    justify-content: flex-end;
  }
}

.main-menu .gr-toggle-arrow {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-menu .gr-toggle-arrow:after {
  display: none;
}

.main-menu .gr-toggle-arrow i {
  font-size: 24px;
  width: auto;
  line-height: 1;
  height: auto;
  font-weight: 900;
  margin-left: 5px;
  transition: .4s;
}

.main-menu .gr-toggle-arrow:hover i {
  transform: rotate(-180deg);
}

.main-menu > li > .nav-link {
  color: var(--color-headings) !important;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: -0.4px;
  text-transform: inherit;
  position: relative;
  transition: .4s;
}

.main-menu > li > .nav-link:focus {
  outline: none;
}

@media (min-width: 992px) {
  .main-menu > li > .nav-link {
    padding-top: 15px !important;
    padding-bottom: 15px !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-left: 18px !important;
    margin-right: 18px !important;
  }
}

.main-menu > li > .nav-link:hover {
  color: #1290a4 !important;
}

.main-menu > li > .nav-link:hover:before {
  transform: scaleX(1);
}

@media (min-width: 992px) {
  .main-menu > li > .nav-link.gr-toggle-arrow {
    padding-right: 30px !important;
  }
  .main-menu > li > .nav-link.gr-toggle-arrow:before {
    width: calc(100% - 8px);
  }
  .main-menu > li > .nav-link.gr-toggle-arrow i {
    position: absolute;
    right: -2px;
  }
}

.main-menu > li.active > .nav-link:before {
  transform: scaleX(1);
}

.main-menu > li:hover > .gr-toggle-arrow i {
  transform: rotate(-180deg);
}

/*----- Dropdown styles
-------------------------*/
.gr-menu-dropdown {
  background-color: transparent;
}

@media (min-width: 992px) {
  .gr-menu-dropdown {
    position: absolute;
    min-width: 227px;
    max-width: 227px;
    box-shadow: 0 52px 54px rgba(65, 62, 101, 0.3);
    border-radius: 8px;
    border: 1px solid var(--border-color-3);
    padding: 15px 0px;
    top: 100%;
    z-index: -99;
    opacity: 0;
    transition: opacity .4s, top .4s;
    pointer-events: none;
    left: 0;
    right: auto;
    border-radius: 0 0 10px 10px;
    border: 1px solid var(--border-color-3);
    background-color: #fff;
    display: block;
    border-top: 3px solid #1290a4;
  }
}

.gr-menu-dropdown > .drop-menu-item {
  color: var(--color-headings);
  font-size: 16px;
  text-transform: inherit;
  font-weight: 700;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}

@media (max-width: 767px) {
  .gr-menu-dropdown > .drop-menu-item {
    padding-right: 0;
  }
}

.gr-menu-dropdown > .drop-menu-item > a {
  color: #000;
}

@media screen and (max-width: 480px) {
  .gr-menu-dropdown > .drop-menu-item > a {
    color: #fff;
  }
}

@media (min-width: 992px) {
  .gr-menu-dropdown > .drop-menu-item > a {
    transition: .4s;
  }
}

.gr-menu-dropdown > .drop-menu-item:hover > a {
  color: #1290a4;
}

.gr-menu-dropdown > .drop-menu-item > .gr-menu-dropdown {
  border-top-color: #4eab08;
}

@media (min-width: 992px) {
  .gr-menu-dropdown > .drop-menu-item > .gr-menu-dropdown {
    top: 10px;
    left: auto;
    right: 0;
    opacity: 0;
    transform: translateX(110%);
    transition: .4s;
    pointer-events: none;
    will-change: transform;
  }
}

@media (min-width: 380px) {
  .gr-menu-dropdown > .drop-menu-item > .gr-menu-dropdown > .drop-menu-item {
    padding-left: 25px;
    padding-right: 25px;
  }
}

@media (min-width: 992px) {
  .gr-menu-dropdown > .drop-menu-item > .gr-menu-dropdown > .drop-menu-item {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.gr-menu-dropdown.dropdown-right {
  left: auto;
  right: -90%;
}

@media (min-width: 992px) {
  .gr-menu-dropdown.dropdown-left {
