.btn-toggle {
  width: 48px;
  height: 24px;
  border-radius: 17px;
  background-color: #f8f8f8 !important;
  position: relative;
  margin: 0 15px;
}

.btn-toggle span {
  width: 16px;
  height: 16px;
  background-color: #1290a4;
  position: absolute;
  left: 5px;
  top: 4px;
  transition: 0.4s;
  border-radius: 500px;
  pointer-events: none;
}

.btn-toggle-2 {
  width: 220px;
  height: 42px;
  border-radius: 30px;
  background-color: #fff !important;
  border: 1px solid #e5e5e5 !important;
  position: relative;
  margin: 0 15px;
  z-index: 1;
}

.btn-toggle-2:before {
  position: absolute;
  content: "Monthly";
  width: 46%;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000;
  height: 34px;
  background: transparent;
  left: 9px;
  top: 4px;
  line-height: 34px;
}

.btn-toggle-2:after {
  position: absolute;
  content: "Yearly";
  width: 46%;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000;
  height: 34px;
  background: transparent;
  right: 9px;
  top: 4px;
  line-height: 34px;
}

.btn-toggle-2 .round {
  width: 50%;
  /* left: 0 !important; */
  height: 34px;
  top: 3px;
  left: 4px;
  background: blue;
  display: block;
  z-index: -1;
}

.btn-toggle-2 span {
  display: none;
}

.btn-toggle[data-value-active="yearly"] {
  background-color: #1290a4 !important;
}

.btn-toggle[data-value-active="yearly"] span {
  left: calc(100% - 25px) !important;
}

.btn-toggle-square {
  height: 65px;
  display: inline-flex;
  position: relative;
  z-index: 1;
  padding: 5px;
  align-items: center;
}

.btn-toggle-square.bg-whisper-3:hover, .btn-toggle-square.bg-whisper-3:focus {
  background: #8e8e8e !important;
}

.btn-toggle-square span {
  min-width: 160px;
  min-height: 50px;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: -0.5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000000;
}

.btn-toggle-square span:first-child {
  border-radius: 10px 0 0 10px;
}

.btn-toggle-square span:nth-child(2) {
  border-radius: 0px 10px 10px 0;
}

.btn-toggle-square span.active {
  color: #fff;
  background: #1290a4;
}

.popular-pricing:before {
  position: absolute;
  content: "";
  left: 0px;
  top: 0px;
  width: 100%;
  height: 4px;
  background: #f46f7c;
  border-radius: 6px;
}

.popular-pricing.popular-pricing-2:before {
  height: 3px;
  top: -3px;
  background: #14ee80;
}

.popular-pricing.popular-pricing-3:before {
  height: 3px;
  top: -3px;
  background: #50e3c2;
}

.active-border {
  position: relative;
}

.active-border:before {
  position: absolute;
  content: "";
  left: 0px;
  top: 0px;
  width: 100%;
  height: 5px;
  background: #5029de;
  border-radius: 5px;
}

