
/* Landing-1,3,4,5 */
.bg-shape {
    min-height: 200px;
    width: 110%;
    border-radius: 50%;
    &:before {
        position: absolute;
        content: "";
        left: -4%;
        top: -10%;
        width: 113%;
        height: 25%;
        background: #fefdfd;
        border-radius: 0% 0% 240% 300%;
        transform: rotate(2deg);
        @include brk-point-max(767px){
            top: -10%;
            height: 18%;
            border-radius: 0% 0% 17% 80%;
            transform: rotate(-27deg);
        }
        @include brk-point-range(480px, 767px){
            top: -7%;
            height: 16%;
            border-radius: 0% 0% 3% 98%;
            transform: rotate(-12deg);
        }
        @include brk-point-range(768px, 991px){
            top: -9%;
            height: 20%;
            transform: rotate(-2deg);
            border-radius: 0% 0% 129% 300%;
        }
        @include brk-point-range(992px, 1200px){
            // transform: rotate(-5deg);
        }
        @include brk-point-range(1201px, 1400px){
            border-radius: 0% 0% 230% 300%;
            transform: rotate(3deg);
        }
    }

    /* Used in Landing-1 */
    &-1 {
        background: url(../image/l1/svg/shape2.svg);
        /* background-repeat: no-repeat; */
        position: absolute;
        bottom: -2px;
        height: 170px;
        width: 101%;
        background-size: cover;
        left: 0%;
        transform: rotate(0deg);
        @include brk-point-max(767px){
            height: 85px;
        }
    }

    /* Used in Landing-3 */
    &-2 {
        background: url(../image/l3/png/hero-bg.png);
        /* background-repeat: no-repeat; */
        position: absolute;
        top: -50%;
        height: 135%;
        width: 135%;
        background-size: cover;
        left: -30%;
        transform: rotate(-11deg);
        z-index: -1;
        
        @include brk-point-range(1201px, 1366px){
            height: 150%;
            width: 135%;
            left: -30%;
            transform: rotate(-19deg);
        }
        
        @include brk-point-range(992px, 1200px){
            height: 136%;
            width: 270%;
            left: -97%;
            transform: rotate(-7deg);
        }
        @include brk-point-max(767px){
            // bottom: -190px;
        }
        
    }

    /* Used in Landing-3 */
    &-3 {
        background: url(../image/l3/svg/testimonial-bg.svg);
        background-repeat: no-repeat;
        position: absolute;
        top: -50%;
        height: 136%;
        width: 135%;
        background-size: cover;
        left: -29%;
        transform: rotate(-3deg);
        z-index: -1;
        
        @include brk-point-range(1201px, 1366px){
            top: -68%;
            height: 173%;
            width: 155%;
            left: -40%;
            transform: rotate(-7deg);
        }
        @include brk-point-range(992px, 1200px){
            height: 152%;
            width: 250%;
            left: -118%;
        }
        @include brk-point-range(768px, 991px){
            height: 143%;
            width: 170%;
            left: -65%;
        }
        @include brk-point-max(767px){
            // bottom: -190px;
        }
        
        @include brk-point-range(480px, 767px){
            top: -88%;
            height: 199%;
            width: 171%;
            left: -64%;
        }
        
    }
    /* Used in Landing-4 */
    &-4 {
        background: url(../image/l4/svg/hero-bg.svg);
        /* background-repeat: no-repeat; */
        position: absolute;
        top: -15px;
        height: 100%;
        width: 100%;
        left: 0;
        transform: rotate(0deg);
        z-index: -1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center right;
    }

    /* Used in Landing-5 */
    &-5 {
        background: url(../image/l5/png/l5-pricing-bg.png);
        /* background-repeat: no-repeat; */
        position: absolute;
        top: 0;
        height: 100%;
        width: 100%;
        left: 0;
        transform: rotate(0deg);
        z-index: -1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center right;
    }
    /* Used in Landing-10 */
    &-6 {
        background: url(../image/l10/svg/shape-1.svg);
        /* background-repeat: no-repeat; */
        position: absolute;
        top: 0;
        height: 115px;
        width: 105%;
        left: 0;
        transform: rotate(0deg);
        z-index: -1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center right;
        @include brk-point-max(767px){
            height: 50px;
        }
    }
    /* Used in Landing-1,4 */
    &-img {
        height: 100%;
        background: url(../image/l1/png/shape1.png);
        background-size: cover;
        background-repeat: no-repeat;
    }

    /* Used in Landing-4 */
    &-img-2 {
        background: url(../image/l4/png/features-bg.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0%;
        @include brk-point-max(991px){
            background-size: cover;
        }
    }

    /* Used in Landing-4 */
    &-img-3 {
        height: 100%;
        background: url(../image/l4/png/cta-bg.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: left bottom;
        width: 97%;
        position: absolute;
        top: 0;

        @include brk-point-max(767px){
            background-position: center right;
            width: 100%;
            left: 0;
        }

        @include brk-point-range(576px, 767px){
            background-position: center left;
        }

        @include desktops{
            background-size: contain;
        }
    }

    /* used in landing-7 */
    
    /* used in landing-7 */
    &-img-5 {
        height: 100%;
        background: url(../image/l8/png/hero-bg.png);
        background-size: cover;
        background-repeat: no-repeat;
    }
}


