[data-pricing-dynamic][data-value-active="yearly"]{
  .dynamic-value{
    &:before{
    display: inline-block;
    content: attr(data-yearly);
    }
  }
  [data-pricing-trigger]{
    background: $primary;
    span{
      left: calc(100% - 24px);
    }
  }
  .btn-toggle-2[data-pricing-trigger]{
    background: $primary;
    span{
      left: calc(100% - 52%);
    }
    &.active {
      &:before {
        color: #000 !important;
      }
      &:after {
        color: #000 !important;
      }
    }
  }
  
}
[data-pricing-dynamic][data-value-active="monthly"]{
  .dynamic-value{
    &:before{
      display: inline-block;
      content: attr(data-monthly);
    }
  }
  
}
.btn-toggle-2[data-pricing-trigger]{
  &.active {
    &:before {
      color: #000 !important;
    }
    &:after {
      color: #000 !important;
    }
  }
}

.dynamic-value{
  &:before{
    display: inline-block;
    content: attr(data-active);
  }
}
.static-value{
  &:before{
    display: inline-block;
    content: attr(data-active);
  }
}
  
.btn-toggle {
  width: 48px;
  height: 24px;
  border-radius: 17px;
  background-color: #f8f8f8 !important;
  position: relative;
  margin: 0 15px;
  span {
    width: 16px;
    height: 16px;
    background-color: $bluechil;
    position: absolute;
    left: 5px;
    top: 4px;
    transition: 0.4s;
    border-radius: 500px;
    pointer-events: none;
  }
  &-2 {
    width: 220px;
    height: 42px;
    border-radius: 30px;
    background-color: #fff !important;
    border: 1px solid #e5e5e5 !important;
    position: relative;
    margin: 0 15px;
    z-index: 1;
    &:before {
      position: absolute;
      content: "Monthly";
      width: 46%;
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      color: #000;
      height: 34px;
      background: transparent;
      left: 9px;
      top: 4px;
      line-height: 34px;
    }
    &:after {
      position: absolute;
      content: "Yearly";
      width: 46%;
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      color: #000;
      height: 34px;
      background: transparent;
      right: 9px;
      top: 4px;
      line-height: 34px;
    }
    .round {
      width: 50%;
      /* left: 0 !important; */
      height: 34px;
      top: 3px;
      left: 4px;
      background: blue;
      display: block;
      z-index: -1;
    }
    span {
      display: none;
    }
    
  }

  &[data-value-active="yearly"] {
    background-color: $bluechil !important;

    span {
      left: calc(100% - 25px) !important;
    }
  }
  &-square{
    height: 65px;
    display: inline-flex;
    position: relative;
    z-index: 1;
    padding: 5px;
    align-items: center;
    &.bg-whisper-3{
      &:hover,&:focus{
        background: $gray!important;
      }
    }
    span{
      min-width: 160px;
      min-height: 50px;
      font-size: 16px;
      font-weight: 700;
      letter-spacing: -0.5px;
      display: flex;
      align-items: center;
      justify-content: center;
      color:$black;
      &:first-child{
        border-radius: 10px 0 0 10px;
      }
      &:nth-child(2){
        border-radius: 0px 10px 10px 0;
      }
      &.active{
        color:#fff;
        background: $bluechil;
      }
    }
    
  }
}


.popular-pricing {
  &:before {
    position: absolute;
    content: "";
    left: 0px;
    top: 0px;
    width: 100%;
    height: 4px;
    background: #f46f7c;
    border-radius: 6px;
  }

  &.popular-pricing-2 {
    &:before {
      height: 3px;
      top: -3px;
      background: $spring-green;
    }
  }

  &.popular-pricing-3 {
    &:before {
      height: 3px;
      top: -3px;
      background: $turquoise;
    }
  }
}

.active-border {
  position: relative;
  &:before {
    position: absolute;
    content: "";
    left: 0px;
    top: 0px;
    width: 100%;
    height: 5px;
    background: #5029de;
    border-radius: 5px;
  }

  
}
  