$shadow-3  : inset 0 1px 20px rgba(0, 0, 0, 0.2), inset 0 1px 10px #ffffff;
$shadow-4  : inset 0 1px 60px rgba(0, 0, 0, 0.1), inset 0 10px 40px #ffffff;
$shadow-5  : 0 12px 24px rgba(0, 0, 0, 0.03);
$shadow-6  : 0 10px 60px rgba(35, 44, 39, 0.05);
$shadow-7  : 0 2px 4px rgba(37, 41, 39, 0.03);
$shadow-8  : 0 30px 60px rgba(49, 70, 60, 0.05);
$shadow-9  : 0 22px 74px rgba(20, 21, 24, 0.18);
$shadow-10  : 0 22px 64px rgba(34, 45, 61, 0.13);
$shadow-11  : 0 6px 10px rgba(0, 0, 0, 0.07);
$shadow-12  : 0 12px 24px rgba(0, 0, 0, 0.06);
$shadow-13  : 0 2px 4px rgba(0, 0, 0, 0.12);
$shadow-14  : 0 2px 4px rgba(0, 0, 0, 0.04);


$gr-shadows: (
  1 : $shadow-1,
  2 : $shadow-2,
  3 : $shadow-3,
  4 : $shadow-4,
  5 : $shadow-5,
  6 : $shadow-6, /* used in landing-6 */
  7 : $shadow-7, /* used in landing-6 */
  8 : $shadow-8, /* used in landing-6 */
  9 : $shadow-9, /* used in landing-7 */
  10 : $shadow-10, /* used in landing- */
  11 : $shadow-11, /* used in landing- */
  12 : $shadow-12, /* used in landing- */
  13 : $shadow-13, /* used in landing- */
  14 : $shadow-14, /* used in landing-10 */
);
