@import url('https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Gothic+A1:wght@800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,600;0,700;1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');
//-------------------------
//--- Theme Typography
//-------------------------
$enable-responsive-font-sizes: true;
$font-family-base:            'Karla', sans-serif;

//custom font-family
$btn-font-family: $font-family-base;
$heading-font-family: $font-family-base;
$paragraph-font-family: $font-family-base;


$multiple-font-family: true;
$font-families: (
  "1": 'Gothic A1',
  "2": 'lucida_granderegular',
  "3": 'Rubik',
  "4": 'Work Sans',
  "5": 'Inter',
  "6": 'Nunito',
  "7": 'Noto Serif',
  "fontName": $font-family-base
);


$headings-font-weight:        700;
// $headings-line-height:        1.5;

//- Display Sizes

$display1-size:               6rem; // About 96px
$display2-size:               5rem; // About 80px
$display3-size:               3.75rem; // About 60px
$display4-size:               3.125rem; // About 50px


//- Font Sizes 
$font-size-base:              1rem; // Assumes the browser default, typically `16px`
$font-size-lg:                $font-size-base * 1.25;
$font-size-sm:                13px;

$h1-font-size:                3rem; //About 48px
$h2-font-size:                2.5rem; //About 40px
$h3-font-size:                2.125rem;  //About 34px
$h4-font-size:                1.5rem; //About 24px
$h5-font-size:                1.3125rem; //About 21px
$h6-font-size:                1rem; //About 16px



$text-base:                  1.125rem; //18px


